import * as React from "react";
import { styled } from "@mui/material/styles";
import AppBar from "@mui/material/AppBar";
import Box from "@mui/material/Box";
import Toolbar from "@mui/material/Toolbar";
import IconButton from "@mui/material/IconButton";
import Typography from "@mui/material/Typography";
import Fab from "@mui/material/Fab";
import InputBase from "@mui/material/InputBase";
import Badge from "@mui/material/Badge";
import MenuItem from "@mui/material/MenuItem";
import Menu from "@mui/material/Menu";
import MenuIcon from "@mui/icons-material/Menu";
import SearchIcon from "@mui/icons-material/Search";
import AccountCircle from "@mui/icons-material/AccountCircle";
import MailIcon from "@mui/icons-material/Mail";
import NotificationsIcon from "@mui/icons-material/Notifications";
import MoreIcon from "@mui/icons-material/MoreVert";

const Search = styled("div")(({ theme }) => ({
  position: "relative",
  borderRadius: theme.shape.borderRadius,
  marginRight: theme.spacing(2),
  marginLeft: 0,
  width: "100%",
  [theme.breakpoints.up("sm")]: {
    marginLeft: theme.spacing(3),
    width: "auto",
  },
}));

const SearchIconWrapper = styled("div")(({ theme }) => ({
  padding: theme.spacing(0, 2),
  height: "100%",
  position: "absolute",
  pointerEvents: "none",
  display: "flex",
  alignItems: "center",
  justifyContent: "center",
}));

const StyledInputBase = styled(InputBase)(({ theme }) => ({
  color: "inherit",
  "& .MuiInputBase-input": {
    padding: theme.spacing(1, 1, 1, 0),
    // vertical padding + font size from searchIcon
    paddingLeft: `calc(1em + ${theme.spacing(4)})`,
    transition: theme.transitions.create("width"),
    width: "100%",
    [theme.breakpoints.up("md")]: {
      width: "20ch",
    },
  },
}));

export default function TaskAppBar() {
  const [anchorEl, setAnchorEl] = React.useState(null);
  const [mobileMoreAnchorEl, setMobileMoreAnchorEl] = React.useState(null);

  const [menuOpen, setMenuOpen] = React.useState(false);
  const [anchorElApp, setAnchorElApp] = React.useState(false);
  const isMenuOpen = Boolean(anchorEl);
  const isMenuAppOpen = Boolean(anchorElApp);
  const isMobileMenuOpen = Boolean(mobileMoreAnchorEl);

  const handleProfileMenuOpen = (event) => {
    console.log("S22");
    setAnchorEl(event.currentTarget);
  };

  const handleMobileMenuClose = () => {
    setMobileMoreAnchorEl(null);
  };

  const handleMenuClose = () => {
    setAnchorEl(null);
    handleMobileMenuClose();
  };

  const handleAppMenuClose = () => {
      console.log('close s01')
    setAnchorElApp(null);
    //handleMobileMenuClose();
  };

  const handleMobileMenuOpen = (event) => {
    console.log("S12");
    setMobileMoreAnchorEl(event.currentTarget);
  };

  const menuId = "primary-search-account-menu";
  const renderProfileMenu = (
    <Menu
      anchorEl={anchorEl}
      anchorOrigin={{
        vertical: "top",
        horizontal: "right",
      }}
      id={menuId}
      keepMounted
      transformOrigin={{
        vertical: "top",
        horizontal: "right",
      }}
      open={isMenuOpen}
      onClose={handleMenuClose}
    >
      <MenuItem onClick={handleMenuClose}>Провиль пользователя</MenuItem>
      <MenuItem onClick={handleMenuClose}>Акаунт</MenuItem>
      <MenuItem onClick={handleMenuClose}>Выйти</MenuItem>
    </Menu>
  );

  function handleClose(url) {
    console.log("A", url);
    window.history.pushState({},'', url);
    const navEvent = new PopStateEvent('popstate');
    window.dispatchEvent(navEvent);
    setAnchorElApp(null);
  };

  const renderAppMenu = (
    <Menu
      id="menu-appbar"
      anchorEl={anchorElApp}
      keepMounted
      open={isMenuAppOpen}
      onClose={handleAppMenuClose}
    >
      <MenuItem onClick={()=>handleClose('/base-cost')}>Стандартная стоимость уборки</MenuItem>
      <MenuItem onClick={()=>handleClose('/performers')}>Исполнители</MenuItem>
      <MenuItem onClick={()=>handleClose('/properties')}>Объекты</MenuItem>
      
    </Menu>
  );

  let closeProfileMenu = () => {
    setMenuOpen(false);
  };

  let closeAppMenu = () => {
    setMenuOpen(false);
  };

  const handleAppMenu = (event) => {
    console.log("S33");
    setAnchorElApp(event.currentTarget);
  };

  const mobileMenuId = "primary-search-account-menu-mobile";
  const renderMobileMenu = (
    <Menu anchorEl={anchorEl} open={menuOpen} onClose={closeProfileMenu}>
      <MenuItem onClick={closeProfileMenu}> ExampleMenuItem </MenuItem>
    </Menu>
  );

  return (
    <header>
      <AppBar position="static">
        <Toolbar style={{ backgroundColor: "#FFD10D", color: "#000000" }}>
          <IconButton
            style={{ marginRight: "16px" }}
            size="large"
            color="inherit"
            sx={{ mr: 2 }}
            aria-haspopup="true"
            onClick={handleAppMenu}
            aria-controls={"ljjjjj"}
          >
            <MenuIcon />
          </IconButton>
         {renderAppMenu}

          <Typography
            variant="h5"
            noWrap
            fontFamily="Oswald"
            component="div"
            sx={{ display: { xs: "none", sm: "block" } }}
          >
            Эксплуатация - Клининг
          </Typography>
          <Search>
            <SearchIconWrapper>
              <SearchIcon />
            </SearchIconWrapper>
            <StyledInputBase
              style={{ fontFamily: "Oswald" }}
              placeholder="Search…"
            />
          </Search>
          <Box sx={{ flexGrow: 1 }} />
          <Box sx={{ display: { xs: "none", md: "flex" } }}>
            <IconButton
              size="large"
              aria-label="show 4 new mails"
              color="inherit"
            >
              <Badge badgeContent={4} color="error">
                <MailIcon />
              </Badge>
            </IconButton>
            <IconButton
              size="large"
              aria-label="show 17 new notifications"
              color="inherit"
            >
              <Badge badgeContent={17} color="error">
                <NotificationsIcon />
              </Badge>
            </IconButton>
            <IconButton
              size="large"
              edge="end"
              aria-label="account of current user"
              aria-controls={menuId}
              aria-haspopup="true"
              onClick={handleProfileMenuOpen}
              color="inherit"
            >
              <AccountCircle />
            </IconButton>
          </Box>
          <Box sx={{ display: { xs: "flex", md: "none" } }}>
            <IconButton
              size="large"
              aria-label="show more"
              aria-controls={mobileMenuId}
              aria-haspopup="true"
              onClick={handleMobileMenuOpen}
              color="inherit"
            >
              <MoreIcon />
            </IconButton>
          </Box>
        </Toolbar>
      </AppBar>
      {renderProfileMenu}
    </header>
  );
}
