import * as React from "react";
import { styled } from "@mui/material/styles";
import Table from "@mui/material/Table";
import TableBody from "@mui/material/TableBody";
import TableCell, { tableCellClasses } from "@mui/material/TableCell";
import TableContainer from "@mui/material/TableContainer";
import TableHead from "@mui/material/TableHead";
import TableRow from "@mui/material/TableRow";
import Paper from "@mui/material/Paper";
import Fab from "@mui/material/Fab";
import AddIcon from "@mui/icons-material/Add";
import Tooltip from "@mui/material/Tooltip";


const StyledFab = styled(Fab)({
  /* position: "absolute",
   zIndex: 1,
   top: 30, */
  left: 0,
  right: 0,
  margin: "0 auto",
});


const StyledTableCell = styled(TableCell)(({ theme }) => ({
  [`&.${tableCellClasses.head}`]: {
    backgroundColor: "rgb(255, 209, 13)",
    color: theme.palette.common.black,
  },
  [`&.${tableCellClasses.body}`]: {
    fontSize: 14,
  },
}));

const StyledTableRow = styled(TableRow)(({ theme }) => ({
  "&:nth-of-type(odd)": {
    backgroundColor: theme.palette.action.hover,
  },
  // hide last border
  "&:last-child td, &:last-child th": {
    border: 0,
  },
}));

function createData(name, calories, fat, carbs, protein) {
  return { name, calories, fat, carbs, protein };
}

const rows = [
  createData("Frozen yoghurt", 159, 6.0, 24, 4.0),
  createData("Ice cream sandwich", 237, 9.0, 37, 4.3),
  createData("Eclair", 262, 16.0, 24, 6.0),
  createData("Cupcake", 305, 3.7, 67, 4.3),
  createData("Gingerbread", 356, 16.0, 49, 3.9),
];

export default function Performers() {
  return (
    <main>
      <Paper
        style={{
          padding: 20,
          margin: 20,
          fontFamily: "Oswald",
          border: "1px solid black",
        }}
      >
        <TableContainer component={Paper} style={{marginBottom:"1em"}}>
          <Table
            sx={{ minWidth: 700, fontFamily: "Oswald" }}
            aria-label="customized table"
          >
            <TableHead>
              <TableRow>
                <StyledTableCell fontFamily="Oswald">
                  Компания исполнитель
                </StyledTableCell>
                <StyledTableCell align="right">Обьектов в обслуживании</StyledTableCell>
                <StyledTableCell align="right">Открытых заявок</StyledTableCell>
                <StyledTableCell align="right">стоимость уборки (М2)</StyledTableCell>
                <StyledTableCell align="right">стоимость мойки витрин (М2)</StyledTableCell>

                <StyledTableCell align="right">
                  Процент исполнения
                </StyledTableCell>
              </TableRow>
            </TableHead>
            <TableBody>
              {rows.map((row) => (
                <StyledTableRow key={row.name}>
                  <StyledTableCell component="th" scope="row">
                    {row.name}
                  </StyledTableCell>
                  <StyledTableCell align="right">
                    {row.calories}
                  </StyledTableCell>
                  <StyledTableCell align="right">{row.fat}</StyledTableCell>
                  <StyledTableCell align="right">12</StyledTableCell>
                  <StyledTableCell align="right">12</StyledTableCell>
                  <StyledTableCell align="right">{row.carbs}</StyledTableCell>
                </StyledTableRow>
              ))}
            </TableBody>
          </Table>
        </TableContainer>
        <StyledFab
          color="secondary"
          sy={{ mr: -3 }}
          aria-label="add"
          href="/cleaning/create-performer"
        >
          <Tooltip title="Добавить нового исполнителя">
            <AddIcon />
          </Tooltip>
        </StyledFab>
      </Paper>
    </main>
  );
}
